.loader {
  color: #fff;
  position: fixed;
  box-sizing: border-box;
  left: -9999px;
  top: -9999px;
  width: 0;
  height: 0;
  overflow: hidden;
  z-index: 999999;
}
.loader:after,
.loader:before {
  box-sizing: border-box;
  display: none;
}
.loader.is-active {
  background-color: rgba(0, 0, 0, 0.85);
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}
.loader.is-active:after,
.loader.is-active:before {
  display: block;
}
.loader-curtain:after,
.loader-curtain:before {
  position: fixed;
  width: 100%;
  top: 50%;
  margin-top: -35px;
  font-size: 150px;
  text-align: center;
  font-family: Helvetica, Arial, sans-serif;
  overflow: hidden;
  line-height: 1.5;
  content: "Loading...";
}
.loader-curtain:before {
  color: #666;
}
.loader-curtain:after {
  color: #fff;
  height: 0;
  animation: curtain 1s linear infinite alternate both;
}
.loader-curtain[data-curtain-text]:not([data-curtain-text=""]):after,
.loader-curtain[data-curtain-text]:not([data-curtain-text=""]):before {
  content: attr(data-curtain-text);
}
.loader-curtain[data-brazilian]:before {
  color: #EB9C37;
}
.loader-curtain[data-brazilian]:after {
  color: #DD4C57;
}
.loader-curtain[data-colorful]:before {
  animation: maskColorful 2s linear infinite alternate both;
}
.loader-curtain[data-colorful]:after {
  animation: curtain 1s linear infinite alternate both,
    maskColorful-front 2s 1s linear infinite alternate both;
  color: #000;
}
@keyframes maskColorful {
  0% {
    color: #3498db;
  }
  49.5% {
    color: #3498db;
  }
  50.5% {
    color: #e74c3c;
  }
  to {
    color: #e74c3c;
  }
}
@keyframes maskColorful-front {
  0% {
    color: #2ecc71;
  }
  49.5% {
    color: #2ecc71;
  }
  50.5% {
    color: #f1c40f;
  }
  to {
    color: #f1c40f;
  }
}
@keyframes curtain {
  0% {
    height: 0;
  }
  to {
    height: 200px;
  }
}

